.left-side {
  width: 40%;
  min-height: calc(100vh - 185px); // –£—á–∏—Ç—ã–≤–∞–µ–º header (80px) + footer (80px) + –æ—Ç—Å—Ç—É–ø—ã (25px)
  background: rgba(40, 40, 40, 0.85); // –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Å–µ—Ä—ã–π —Ñ–æ–Ω –∫–∞–∫ —É PDA
  backdrop-filter: blur(8px);
  border: 2px solid #666; // –°–µ—Ä–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ –∫–∞–∫ —É PDA
  border-radius: 15px; // –ó–∞–∫—Ä—É–≥–ª–µ–Ω–∏—è –∫–∞–∫ —É PDA
  position: fixed;
  left: 55px; // –ü–µ—Ä–µ–º–µ—â–∞–µ–º –ª–µ–≤—É—é –≥—Ä–∞–Ω–∏—Ü—É –Ω–∞ 55px –≤–ø—Ä–∞–≤–æ
  top: 100px; // –ù–∞ 20px –Ω–∏–∂–µ navbar –¥–ª—è –≤–∏–¥–∏–º–æ—Å—Ç–∏ –≤–µ—Ä—Ö–Ω–µ–π –≥—Ä–∞–Ω–∏—Ü—ã
  z-index: 5; // –ù–∏–∂–µ navbar (z-index: 100)
  padding: 2rem 1rem;
  box-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.6),
    inset 0 1px 0 rgba(255, 255, 255, 0.1); // –û–±—ä—ë–º–Ω–∞—è —Ç–µ–Ω—å –∏ –≤–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –ø–æ–¥—Å–≤–µ—Ç–∫–∞

  .left-side-content {
    .stalker-buttons {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      margin-left: 25px;

      .stalker-btn {
        display: flex;
        align-items: center;
        gap: 0.8rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –∏–∫–æ–Ω–∫–æ–π –∏ —Ç–µ–∫—Å—Ç–æ–º
        padding: 1rem 1rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –≤—ã—Å–æ—Ç—É –∏ –æ—Ç—Å—Ç—É–ø—ã
        margin: 0.2rem 0; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã –º–µ–∂–¥—É –∫–Ω–æ–ø–∫–∞–º–∏
        border: none;
        border-radius: 0;
        cursor: pointer;
        transition: all 0.2s ease;
        font-weight: 500;
        font-size: 1.1rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞
        font-family: 'Courier New', monospace;
        text-align: left;
        width: 100%;
        background: transparent;
        color: #90EE90; // –°–≤–µ—Ç–ª–æ-–∑–µ–ª—ë–Ω—ã–π –∫–∞–∫ –Ω–∞ PDA

        .btn-icon {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-size: 1.4rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä –∏–∫–æ–Ω–æ–∫

          .list-icon, .plus-icon, .money-icon, .add-search-icon, .search-icon, .chart-icon, .wanted-archive-icon, .add-wanted-icon, .user-management-icon, .users-groups-icon, .test-icon {
            font-size: 1.8rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —ç–º–æ–¥–∑–∏
            font-weight: bold;
            filter: grayscale(100%) brightness(1.5); // –ë–µ–ª–æ-—Å–µ—Ä—ã–µ —ç–º–æ–¥–∑–∏
            color: #cccccc;
          }

          .radiation-icon {
            color: #ff8c00; // –û—Ä–∞–Ω–∂–µ–≤—ã–π —Ü–≤–µ—Ç
            font-size: 1.6rem; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —Ä–∞–¥–∏–∞—Ü–∏–∏
            animation: pulse 2s infinite;
            filter: none; // –£–±–∏—Ä–∞–µ–º —Å–µ—Ä—ã–π —Ñ–∏–ª—å—Ç—Ä –¥–ª—è –æ—Ä–∞–Ω–∂–µ–≤–æ–≥–æ —Ü–≤–µ—Ç–∞
          }
        }

        .btn-text {
          flex: 1;
          color: #90EE90;
        }

        &:hover {
          background: rgba(90, 238, 144, 0.1);
          transform: translateX(3px);
        }

        &.active {
          background: rgba(90, 238, 144, 0.15); // –ü–æ—Å—Ç–æ—è–Ω–Ω–∞—è –ø–æ–¥—Å–≤–µ—Ç–∫–∞ –∫–∞–∫ –ø—Ä–∏ hover
          color: #90EE90;
          font-weight: 600;
          border-left: 3px solid #90EE90; // –ó–µ–ª—ë–Ω–∞—è –ª–µ–≤–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ –¥–ª—è –∞–∫—Ç–∏–≤–Ω–æ–π –∫–Ω–æ–ø–∫–∏
          box-shadow: inset 0 0 10px rgba(144, 238, 144, 0.2); // –í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –ø–æ–¥—Å–≤–µ—Ç–∫–∞
          transform: translateX(3px); // –ù–µ–±–æ–ª—å—à–æ–π —Å–¥–≤–∏–≥ –≤–ø—Ä–∞–≤–æ –∫–∞–∫ –ø—Ä–∏ hover
        }

        &.archive-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.add-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.income-expense-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.add-search-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.search-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.add-wanted-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.wanted-archive-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.user-management-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.users-groups-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.finances-btn {
          color: #90EE90;

          &:hover {
            background: rgba(90, 238, 144, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #90EE90;
          }
        }

        &.test-btn {
          color: #ff6b6b; // –ö—Ä–∞—Å–Ω—ã–π —Ü–≤–µ—Ç –¥–ª—è —Ç–µ—Å—Ç–æ–≤–æ–π –∫–Ω–æ–ø–∫–∏
          border: 1px solid #ff6b6b;

          &:hover {
            background: rgba(255, 107, 107, 0.1);
            transform: translateX(3px);
          }

          .btn-text {
            color: #ff6b6b;
          }
        }
      }
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .left-side {
    width: 100%;
    position: relative;
    min-height: auto;
    border-right: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
}
